import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import Home from './Home';
import Influencer from './Influencer';
import JoinUs from './JoinUs';
import Panduan from './Panduan';
import BusinessInquiry from './BusinessInquiry';

function App() {
    return (
        <Router>
            <Route exact path="/" component={Home}></Route>
            <Route path="/corporate_influencer" component={Influencer}></Route>
            <Route path="/join_us" component={JoinUs}></Route>
            <Route path="/panduan" component={Panduan}></Route>
            <Route path="/business_inquiry" component={BusinessInquiry}></Route>
        </Router>
    )
}

export default App;
